body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
    overflow-x: hidden;
}

.App {

    .divSpace-working {
        height: 100vh;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 64px;
        flex-direction: column;
    }

    .App {
        display: flex;
        flex-direction: column;
    }

    .page_container {
        display: flex;
        flex-direction: row;
    }

    .background_color_pink {
        background-color: #fff6f5;
    }

    .box-container-working {
        min-height: 200px;
        display: flex;
        justify-content: center;
        justify-content: center;
        margin-bottom: 200vh;
        background-color: #fff6f5;
    }

    .partecipazione {
        height: 80vh;
        width: 250px;
        text-align: center;
        position: absolute;
        overflow: hidden;
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        object-fit: contain;
    }

    .partecipazione_front {
        background-image: url(../../img/part_front.png);
    }

    .partecipazione_back {
        background-image: url(../../img/part_back.png);
    }

    .envelopeContainer {
        display: flex;
        justify-content: center;
        background-color: #fff6f5;
        width: 100%;
        height: 100vh;
    }

    .envelope {
        height: 70%;
        width: 250px;
        text-align: center;
        left: 0px;
        top: 0px;
        background-color: #faf9f6;
        opacity: 0.95;
        z-index: 3;
        position: absolute;
    }

    .rotating_part {
        height: 33.5%;
        width: 250px;
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        position: absolute;
        bottom: -9%;
        z-index: 9999;
        object-fit: contain;
    }

    .rotating_part_front {
        z-index: 9999;
    }

    .rotating_part_back {
        z-index: 999;
    }

    .scroll_element {
        position: absolute;
        height: 900vh;
        width: 10px;
        top: 0;
        z-index: 0;
    }

    .img_contain {
        object-fit: contain;
        width: 100%
    }
}

